import Index from 'pages/Index'
import Ballot from 'pages/ballot/App'
import ballot_routes from 'pages/ballot/routes'

export default [
  {
    path: '/ballot',
    component: Ballot,
    children: ballot_routes
  }, {
    path: '/home',
    component: Index
  }, {
    path: '/',
    redirect: '/home'
  }
]


