import Vue from 'vue'
import VueRouter from 'vue-router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import routes from './routes'
import App from './App.vue'

Vue.use(VueRouter)
Vue.use(ElementUI)

const router = new VueRouter({
  routes
})

const app = new Vue({
  router,
  ...App
}).$mount('#app')



